  color: #283645;
  text-align: center;
  min-height: 100%;
  margin: 0;
  padding: 0;
}
.page-header {
  text-align: left;
  position: absolute;
  width: 100%;
  top: 0%;
  background-color: #283645;
  z-index: 2;
}
@media (min-width: 1200px) {
  .page-header {
    background-color: rgba(0, 0, 0, 0.3);
  }
}
@media (min-width: 1200px) {
  .page-header__wrapper {
    display: flex;
    justify-content: space-between;
    width: 1200px;
    margin: 0 auto;
  }
}
.page-header__logo img {
  width: 74px;
  height: 23px;
  padding: 22px 20px 15px;
}
@media (min-width: 660px) {
  .page-header__logo img {
    width: 145px;
    height: 40px;
    display: block;
    padding: 27px 20px 43px;
  }
}
@media (min-width: 1200px) {
  .page-header__logo img {
    display: none;
  }
}
.page-header__logo-desktop {
  width: 147px;
  height: 40px;
  display: none;
  padding: 27px 5px 43px 0;
  margin-left: 130px;
}
@media (min-width: 1200px) {
  .page-header__logo-desktop {
    display: block;
  }
}
.page-header__logo-desktop:hover path,
.page-header__logo-desktop:focus path {
  fill: #D22856;
}
.page-header__logo-desktop:active path {
  fill: rgba(255, 255, 255, 0.3);
}
.page-header--closed {
  background-color: rgba(0, 0, 0, 0.3);
}
.page-header .page-header__nav {
  display: block;
}
.page-header--closed .page-header__nav {
  display: none;
}
.page-header .page-header__toggle {
  position: absolute;
  top: 23px;
  right: 20px;
  z-index: 2;
  display: block;
  width: 23px;
  height: 23px;
  background-color: transparent;
  cursor: pointer;
  border: none;
  padding: 0;
}
@media (min-width: 660px) {
  .page-header .page-header__toggle {
    top: 43px;
  }
}
@media (min-width: 1200px) {
  .page-header .page-header__toggle {
    display: none;
  }
}
.page-header--closed .page-header__toggle {
  position: absolute;
  top: 21px;
  right: 20px;
  z-index: 2;
  display: block;
  width: 50px;
  height: 24px;
  background-color: transparent;
  cursor: pointer;
  border: none;
  padding: 0;
}
@media (min-width: 660px) {
  .page-header--closed .page-header__toggle {
    top: 43px;
  }
}
@media (min-width: 1200px) {
  .page-header--closed .page-header__toggle {
    display: none;
  }
}
.page-header__button--closed {
  width: 50px;
  height: 24px;
  display: none;
}
.page-header__button--opened {
  width: 23px;
  height: 23px;
}
.page-header__button {
  color: #FFFFFF;
}
.page-header__button:hover,
.page-header__button:focus {
  color: #D22856;
}
.page-header__button:active {
  color: rgba(210, 40, 86, 0.3);
}
.page-header--closed .page-header__button--opened {
  display: none;
}
.page-header--closed .page-header__button--closed {
  display: block;
}
